import styled from "styled-components";

const FooterDiv = styled.div`
  height: 5vh;
  padding: 0;
  margin: 0;
  background: yellow;
  color: white;

  a {
    color: white;
  }
`;

function Footer() {
  return (
    <FooterDiv>
      <div className="footerDiv">Footer</div>
    </FooterDiv>
  );
}

export default Footer;
